angular.module('empdb').run(['$templateCache', function($templateCache) {
  'use strict';

  $templateCache.put('modals/employee-form.html',
    "<form name=modalForm class=form-horizontal ng-submit=save()><div class=modal-header><h3 class=modal-title>{{title}}</h3></div><div class=modal-body><div class=form-group><label for=firstname class=\"col-sm-2 control-label\">Förnamn</label><div class=col-sm-10><input type=text class=form-control id=firstname ng-model=item.firstname placeholder=Förnamn></div></div><div class=form-group><label for=lastname class=\"col-sm-2 control-label\">Efternamn</label><div class=col-sm-10><input type=text class=form-control id=lastname ng-model=item.lastname placeholder=Efternamn></div></div></div><div class=modal-footer><button class=\"btn btn-success\" type=submit ng-class=\"{'disabled':modalForm.$pristine || modalForm.$invalid}\">Spara</button> <button class=\"btn btn-default\" type=button ng-click=cancel()>Avbryt</button></div></form>"
  );

}]);
